 }
 .FormWithSteps input[type=text] {
     width: 65vw;
     padding: 1em;
     margin-top: 0.6em;
     border: none;
     border-bottom: 1px solid grey;
     box-sizing: border-box;
 }
 
 .FormWithSteps input[type=text]:focus,
 .FormWithSteps input[type=number]:focus {
     
 }
 
 /* Chrome, Safari, Edge, Opera */
 
 .FormWithSteps input::-webkit-outer-spin-button, .FormWithSteps input::-webkit-inner-spin-button {
     -webkit-appearance: none;
     margin: 0;
     padding: 0.6em;
     margin-top: 0.6em;
     border: none;
     border-bottom: 1px solid grey;
     box-sizing: border-box;
 }
 
 /* Firefox */
 
 .FormWithSteps input[type=number] {
     -moz-appearance: textfield;
     padding: 0.6em;
     margin-top: 0.6em;
     border: none;
     border-bottom: 1px solid grey;
     box-sizing: border-box;
 }
 
